body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.game {
  width: 627px;
  margin: 0px auto;
  text-align: center;
}
  h1{
    margin: 10px 0px 3px 0px;
  }
  header{
    width: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 0px 0px -6px 31px;
    -webkit-align-items: center;
            align-items: center
  }
    .status{
      font-size: 20px;
      margin: 5px 0px 5px 2px;
    }
    .btn{
      height: 40px
    }

  .container{
    width: 627px;
    height: 627px;
    margin: 0px 0px 0px -25px;
  }
  .square_bg{
    width: 646px;
  }
    .block{
      height: 34px;
      width: 34px;
      float: left;
      box-sizing: border-box;
    }
    .square {
      background: rgba(243, 193, 75, 0);
      border: 1px solid rgba(0, 0, 0, 0);
      position: relative;
      z-index: 30;
    }
  .board_bg{
    width: 612px;
  }
    .board{
      background: rgba(243, 193, 75);
      border: 1px solid rgba(6, 6, 6, 0.614);
      position: relative;
      top: -626px;
      right: -17px;
    }
  
    .chess{
      border-radius: 50%;
      padding: 11px;
      position: absolute;
      top: 4px;
      left: 4px;
      z-index: 50;
      border: 1px solid #999999df
    }
    .black{
      background: black;
      color: black;
    }
    .white{
    background: white;
    color: white;
    }

.square:focus {
  outline: none;
}
